import { AuthService } from 'src/app/_services/auth.service';
import { Component, HostListener, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CurrentRouteService } from 'src/app/_services/current-route.service';
import { SettingsService } from 'src/app/_services/settings.service';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit {
  openMenuElm = false;
  public isScrolling: boolean;
  showSettings = false;
  currentUrl = '';
  openSearchElm = false;


  constructor(
    public _currentRoute: CurrentRouteService,
    private _settings: SettingsService,
    private router: Router,
    public auth: AuthService
  ) {
    this.isScrolling = false;
  }

  ngOnInit(): void {
  }

  @HostListener('window', ['$event'])
  @HostListener('window:scroll', ['$event'])
  onScrollEvent($event) {
    if (window.scrollY > 100) {
      this.isScrolling = true
    } else {
      this.isScrolling = false
    }
    if (window.innerWidth > 1439) {
      this.openMenuElm = false;
      this.openSearchElm = false
    }
  }

  navigateTo(route) {
    this._settings.navigate.next(route);
    if (this._currentRoute.url != '/home') {
      this.router.navigate(['/home'])
    }
  }

  userLogout() {
    this.showSettings = false
    this._settings.logout = true
  }

  navigatetoUserSettings() {
    this.showSettings = false
    this.router.navigate(['/user/settings'])
  }

  openMenu() {
    this.openMenuElm = true
  }

  openSeach() {
    this.openSearchElm = !this.openMenuElm
  }

}
