import { Component, OnInit } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { FaqModel } from '../_models/faq.model';
import { QueriesService } from '../_services/queries.service';
import { map } from 'rxjs/operators';

@Component({
  selector: 'app-faq',
  templateUrl: './faq.component.html',
  styleUrls: ['./faq.component.scss']
})
export class FaqComponent implements OnInit {
  isOpened: number = null
  openAll: boolean
  faq: FaqModel[] = []

  constructor(
    private query: QueriesService,
    private sanitizer: DomSanitizer
  ) { }

  ngOnInit(): void {
    this.getFaq()
  }

  openAccordeon(id) {
    if (this.isOpened == id) {
      this.isOpened = undefined
      this.openAll = false
    } else {
      this.isOpened = id
      this.openAll = false
    }
  }

  openAllAccordeons() {
    this.isOpened = undefined
    this.openAll = !this.openAll
  }

  getFaq() {
    this.query.getFaq()
    .subscribe((data: FaqModel[]) => {
      this.faq = data.map(e => {
        this.sanitizer.bypassSecurityTrustHtml(e.description) 
        return e
      })
    })
  }


}
